(function () {
  'use strict';

  angular.module('BlurAdmin.pages.version.executive')
      .controller('CurrentKpiCtrl', CurrentKpiCtrl);

  function CurrentKpiCtrl ($scope, $window, $stateParams) {
    var _ = $window._;
    var kNdx = $scope.$parent.n;
    var sParm = _.camelCase($stateParams.type.replace(' ',''));
    $scope.perKpi = {
      data: [],
      label: []
    };
    $scope.kpiName = $stateParams.type;
    var metrics = [
      {
        name: 'revenue',
        type: 'zCurrency',
        value: 'revenueDollar'
      },
      {
        name: 'ebitPercent',
        type: 'zPercent',
        sub: 'ebitPercentScore'
      },
      {
        name: 'iph',
        type: 'zNumber',
        sub: 'avgTktVolume'
      },
      {
        name: 'costPerTkt',
        type: 'currency',
        sub: 'tktVolume'
      },
      {
        name: 'ebitPerTkt',
        type: 'currency',
        sub: 'ebitMin'
      },
      {
        name: 'revPerHead',
        type: 'currency',
        sub: 'avgTktFte'
      }
    ];

    $scope.perKpi.series = [$stateParams.type];
    var tmpArr = [];
    _.forEach(kNdx.perMonth.all(), function(val, key) {
      $scope.perKpi.label.push('' + val.key.getFullYear() + ' - ' + val.key.toDateString().split(' ')[1]);
      tmpArr.push(val.value[sParm]);
    });
    $scope.perKpi.data.push(tmpArr);

    $scope.perKpi.options = {
      //fill: true,
      //backgroundColor: 'rgba(151, 187, 205, 0.1)',
      scales: {
        yAxes: [
          {
            fill: false,
            ticks: {
              callback: function (val, ind, vals) {
                var numFormat = _.filter(metrics, ['name', sParm])[0];
                if (_.lowerCase(numFormat.type).indexOf('percent') !== -1) {
                  return val.toPrecision(2) + '%';
                } else if (_.lowerCase(numFormat.type).indexOf('currency') !== -1) {
                  return '$' + val.toPrecision(2);
                } else {
                  return val.toString();
                }
              },
              beginAtZero: true
            }
          }
        ]
      },
      tooltips: {
        custom: function (tp) {
          console.log(tp);
        }
      }
    };

    $scope.labels =["May", "Jun", "Jul", "Aug", "Sep"];
    $scope.data = [
      [65, 59, 90, 81, 56],
      [28, 48, 40, 19, 88]
    ];
    $scope.series = ['Product A', 'Product B'];


    $scope.changeData = function () {
      $scope.data[0] = shuffle($scope.data[0]);
      $scope.data[1] = shuffle($scope.data[1]);
    };

    function shuffle(o){
      for(var j, x, i = o.length; i; j = Math.floor(Math.random() * i), x = o[--i], o[i] = o[j], o[j] = x){}
      return o;
    }


  }

})();

